/*Social Icons*/

.social-btns {
  overflow: hidden;
}
.social-btns .btn, .social-btns .btn:before, .social-btns .btn .fa {
  transition: all 0.35s;
  transition-timing-function: cubic-bezier(0.31, -0.105, 0.43, 1.59);
}
.social-btns .btn:before {
  top: 90%;
  left: -110%;
}
.social-btns .btn .fa {
  transform: scale(0.8);
}

.social-btns .btn.linkedin:before {background: #0077B5;}
.social-btns .btn.linkedin .fa {color: #0077B5;}

.social-btns .btn.github:before {background: #1a1e22;}
.social-btns .btn.github .fa {color: #1a1e22;}

.social-btns .btn.envelope-square:before {background: rgb(135, 183, 255);}
.social-btns .btn.envelope-square .fa {color: rgb(135, 183, 255);}

.social-btns .btn:focus:before, .social-btns .btn:hover:before {
  top: -10%;
  left: -10%;
}
.social-btns .btn:focus .fa, .social-btns .btn:hover .fa {
  color: #fff;
  transform: scale(1);
}
.social-btns {
  font-size: 0;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.social-btns .btn {
  display: inline-block;
  background: #fff;
  width: 50px;
  height: 50px;
  line-height: 50px;
  margin-right: 10px;
  text-align: center;
  position: relative;
  overflow: hidden;
  border-radius: 30%;
  box-shadow: 0 7px 18px -7px rgba(0,0,0,0.18);
  opacity: 1;
}
.social-btns .btn:before {
  content: '';
  width: 120%;
  height: 120%;
  position: absolute;
  transform: rotate(45deg);
}
.social-btns .btn .fa {
  font-size: 38px;
  vertical-align: middle;
}

